* {
  box-sizing: border-box;
}

html,
body {
  font-size: 16px;
  height: 100%;
  font-family: Sans-Serif;
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
}

iframe {
  border: 0;
  position: fixed;
  top: 60px;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: calc(100% - 80px);
}